<template>
  <div id = "container">
    <div class="createRoom" v-if="creating">
      <div class="close" @click="close">
        <el-button size="mini" type="danger" icon="el-icon-close"></el-button>
      </div>
      <div class="createRoomInput">
        <el-input v-model="roomName" placeholder="请输入房间名"></el-input>
      </div>
      <div class="createRoomButton" @click="createRoom">
        <el-button>创建房间</el-button>
      </div>
    </div>
    <div id = "rooms">
    <Room v-for="room in rooms" :key="room.id" :id="room.id" :name="room.name" :players="room.players" @click="enterRoom(room.id)"></Room>
    </div>
    <el-button id="displayCreateRoom" type="primary" @click="displayCreateRoom">
      创建房间
    </el-button>
  </div>
</template>

<script>
import Room from './components/Room.vue'
import { ref } from 'vue'

export default {
  name: 'App',
  components: {
    Room
  },
  setup(){
    return{
      roomName:ref('')
    }
  },
  data(){
    return {
      rooms:[],
      creating:false
    }
  },
  methods:{
    displayCreateRoom:function(){
      this.creating = true;
    },
    close:function(){
      this.creating=false;
    },
    createRoom:function(){
      this.$axios.get("/createRoom",{
        params: {
          roomName:this.roomName
        }
      })
      .then((response)=>{
        if(response.status == 200 && response.data > 0)
        {
          sessionStorage.setItem("roomId",response.data);
          window.location.href="/room/"+response.data;
        }
      });
    },
    enterRoom:function(roomId){
      sessionStorage.setItem("roomId",roomId);
      window.location.href="/room/"+roomId;
    }
  },
  mounted:function(){
    this.$axios.get("/rooms").then((response)=>{
        this.rooms = response.data;
      });
  },
}

</script>

