    padding:0;
    background-color: rgb(0, 250, 229);
    border-radius: 3% 3% 0 0;
    overflow-y:scroll;
    display: flex;
    flex-wrap: wrap;
  }
  #container{
    justify-content: center;
    align-items: center;
    width: 1000px;
    height: 600px;
    margin: 100px auto;
    padding: 0px;
    background-color: aqua;
    border-radius: 3%;
  }

  #displayCreateRoom{
    position:relative;
    font-size:100%;
    left:40%;
